import { makeAutoObservable } from 'mobx'

export class GameStore {
  rootStore
  puzzleState = ''
  movesMade = 0
  isPuzzleSolved = false
  instaSolve = false
  page = 'practice'
  bottomBarMode = ''
  attributionHtml = ''
  adSpaceHeight = 60
  menuDrawerOpen = false
  doRevealSelectedWord = false
  isWordSelected = false
  helpOpen = false

  constructor(rootStore) {
    this.rootStore = rootStore

    makeAutoObservable(this, {})
  }
  setPuzzleState(state) {
    if (!['puzzleLoaded', 'playing', 'succeeded'].includes(state)) {
      throw new Error(`invalid state: ${state}`)
    }
    this.puzzleState = state
  }

  resetMoves() {
    this.movesMade = 0
    this.isPuzzleSolved = false
  }

  incrementMoveCount() {
    this.movesMade += 1
  }

  setIsPuzzleSolved(v) {
    this.isPuzzleSolved = v
  }

  toggleInstaSolve(v) {
    this.instaSolve = !this.instaSolve
  }

  setAttributionHtml(v) {
    this.attributionHtml = v
  }

  setPage(v) {
    this.page = v
  }

  toggleDrawerOpen() {
    this.menuDrawerOpen = !this.menuDrawerOpen
  }

  setDoRevealSelectedWord(v) {
    this.doRevealSelectedWord = v
  }

  setIsWordSelected(v) {
    this.isWordSelected = v
  }

  setHelpOpen(v) {
    this.helpOpen = v
  }

  setBottomBarMode(v) {
    this.bottomBarMode = v
  }

  get inTutorial() {
    return this.page === 'tutorial'
  }
}
