import { Box, Rating } from '@mui/material'
import { observer } from 'mobx-react-lite'
import { useContext } from 'react'
import { RootStoreContext } from '../store/rootStoreContext.js'
import { Circle as ScoreIcon } from '@mui/icons-material'

const ScoreDisplay = (props) => {
  const [root] = useContext(RootStoreContext)

  return (
    <Rating
      value={Math.max(0, root.puzzleStore.current?.score / 2)}
      precision={0.5}
      readOnly={true}
      size='small'
      icon={<ScoreIcon fontSize='inherit' sx={{ color: '#6aa84f' }} />}
      emptyIcon={<ScoreIcon fontSize='inherit' />}
    />
  )
}

export default observer(ScoreDisplay)
