import { useContext, useRef, useEffect } from 'react'
import { observer } from 'mobx-react-lite'
import { Box } from '@mui/material'
import { RootStoreContext } from '../../store/rootStoreContext'
import QuoteArea from '../quoteArea'
import Slider from '../slider'
import Attribution from '../attribution'
import SuccessDisplay from '../successDisplay'
import ChatBubble from '../chatBubble'
import { Case, Switch } from '../helper/switch'
import ScoreDisplay from '../scoreDisplay'

const Tutorial = (props) => {
  const [root] = useContext(RootStoreContext)
  const hudTopRef = useRef()

  useEffect(() => {
    root.gameStore.setBottomBarMode('singleLabel')
  }, [])

  useEffect(() => {
    root.puzzleStore.loadPuzzleById('tut1')
  }, [root.puzzleStore])

  const tutPage = root.puzzleStore.current?.id

  const getSliderYTranslate = () => {
    // how much to translate slider from the top of
    // it's parent area

    if (!tutPage || !hudTopRef.current) {
      return 20
    }

    if (tutPage !== 'tut3' || root.gameStore.puzzleState !== 'succeeded') {
      // roughly centered vertically
      const hudTopHeight = hudTopRef.current.offsetHeight
      const sentenceHeight = document.getElementById('sentence').offsetHeight
      const halfParentHeight =
        document.getElementById('top-layout').offsetHeight * 0.5 - 32
      return halfParentHeight - 0.5 * sentenceHeight - hudTopHeight
    } else {
      return 80
    }
  }

  const successTextStyle = { fontSize: '20pt', p: '20px' }

  let successMessage = ''
  switch (tutPage) {
    case 'tut1':
      successMessage = <Box sx={successTextStyle}>Well done!</Box>
      break
    case 'tut2':
      successMessage = (
        <Box sx={successTextStyle}>Nice job, you're getting it.</Box>
      )
      break
    case 'tut3':
      successMessage = (
        <Box sx={successTextStyle}>
          Great work, you've got it figured out. <br /> <br />
          Now choose <b>daily</b> or <b>practice</b> play below.
        </Box>
      )
      break
    default:
      break
  }

  const bubbleMessage1 = (
    <Box component='span'>
      Your goal: fix the order of the words.
      <br />
      Tap the two{' '}
      <Box component='span' className='incorrect' sx={{ fontWeight: 800 }}>
        gray words
      </Box>{' '}
      to swap them.
    </Box>
  )

  const bubbleMessage2 = (
    <Box component='span'>Keep swapping until it's all the words green.</Box>
  )

  const bubbleMessage3 = (
    <Box component='span'>Use a hint if you get stuck!</Box>
  )

  return (
    <Slider
      translateAmount={getSliderYTranslate()}
      isTranslated={root.gameStore.puzzleState !== 'succeeded'}
    >
      <Box
        id='hudTop'
        ref={hudTopRef}
        sx={{
          display: tutPage !== 'tut1' ? 'flex' : 'none',
          flexDirection: 'row',
          justifyContent: 'right',
          flexWrap: 'nowrap',
          width: '100%',
        }}
      >
        <ScoreDisplay id='scoreBar' />
      </Box>
      <QuoteArea />

      <Attribution
        show={root.gameStore.puzzleState === 'succeeded'}
        html={
          root.gameStore.puzzleState === 'succeeded'
            ? root.gameStore.attributionHtml
            : ''
        }
      />
      <Switch test={tutPage}>
        <Case value={'tut1'}>
          <ChatBubble
            anchorElementId={'sentence'}
            showBubble={root.gameStore.puzzleState === 'playing'}
            message={bubbleMessage1}
            size={260}
            justify='center'
          />
        </Case>
        <Case value={'tut2'}>
          <ChatBubble
            anchorElementId={'hudTop'}
            showBubble={root.gameStore.puzzleState === 'playing'}
            message={bubbleMessage2}
            size={235}
            justify='right'
          />
        </Case>
        <Case value={'tut3'}>
          <ChatBubble
            anchorElementId={'scoreBar'}
            showBubble={root.gameStore.puzzleState === 'playing'}
            message={bubbleMessage3}
            size={150}
            justify='left'
            flipX={true}
          />
        </Case>
        <Case default></Case>
      </Switch>

      {root.gameStore.puzzleState === 'succeeded' && (
        <Box sx={{ mt: 5 }}>
          <SuccessDisplay
            showStats={false}
            message={successMessage}
            applauseDelay={1500}
            fadeOut={tutPage !== 'tut3'}
            onApplauseEnter={() => {
              if (tutPage === 'tut3') {
                root.gameStore.setBottomBarMode('doubleLabel')
              }
            }}
            onApplauseComplete={() => {
              switch (tutPage) {
                case 'tut1':
                  root.puzzleStore.loadPuzzleById('tut2')
                  break
                case 'tut2':
                  root.puzzleStore.loadPuzzleById('tut3')
                  break
                case 'tut3':
                  break
                default:
                  break
              }
            }}
          />
        </Box>
      )}

      {/* todo: insert into SuccessDisplay <HealthBar sx={{ pr: '4px' }} health={root.gameStore.health} /> */}
    </Slider>
  )
}

export default observer(Tutorial)
